import React, { Component } from "react";
// import logo from './logo.svg';
import "./App.scss";
import Routes from "./Routes";
import { BrowserRouter } from "react-router-dom";

class App extends Component {
  render() {
    return (
      <div className="App">
        <BrowserRouter>
          <Routes />
        </BrowserRouter>
      </div>
    );
  }
}

export default App;



